.footer {
  background: #3f4e65;
  box-shadow: 0px 15px 70px -11px rgba(43, 28, 1, 0.04);
  color: #ffffff;
  padding: 71px 0 0 0;
  display: flex;
  justify-content: space-between;
  //   :last-child{
  //     margin-right: 37px;
  // }
  @media (max-width: 970px) {
    // flex-direction: column;
    padding: 14px;
    flex-wrap: wrap;
    align-items: center;
  }


  &__left {
    margin-right: 59px;
    margin-bottom: 116px;
    @media (max-width: 970px) {
      // flex-direction: column;
  
        margin-bottom: 20px;
        margin-right: auto;
    
      // padding: 14px;
      flex-wrap: wrap;
      align-items: center;
    }
  }

  &__img {
    margin-bottom: 15px;
  }

  &__title {
    width: 320px;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    margin-bottom: 45px;
    @media (max-width: 970px) {
      width: 100%;
      margin-bottom: 15px;
      font-size: 12px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    margin-bottom: 14px;
  }

  &__center {
    display: flex;
    flex-direction: column;
    margin-right: 95px;
    @media (max-width: 970px) {
    
      margin-right: 45px;
    }

  }

  &__center-last {
    margin-right: 37px
  }

  &__category {
    font-weight: 600;
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 25px;
  }

  &__links {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__item {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    @media (max-width: 970px) {
      font-size: 12px;
    }
  }

  &__pricebtn {
    height: 59px;
    width: 214px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 10px 19px;
    gap: 12px;
    background: #ffc85e;
    border-radius: 79px;

    @media (max-width: 970px) {
      height: 42px;
      width: 166px;
    }
  }

  &__seti {
  }

  &__right {
    display: flex;
    flex-direction: column;
  }

  &__cards {
  }
}
.input-group {
  margin-top: 14px;
  position: relative;

  &__input {
    width: 282px;
    height: 59px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 13px 10px 13px 20px;
    gap: 10px;
    background: #ffffff;
    border-radius: 36px;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: #5c6370;
  }

  &__btn {
    top: 8px;
    left: 233px;
    position: absolute;
  }
}



.footer__mobil-activ {
  display: none;
  @media (max-width: 970px) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    // gap: 20px;
  }
}


.mobile-activ {
  @media (max-width: 970px) {
    display: none;
  }
 
}


.footer__category-mobil {
  display: flex;
  
  @media (max-width: 970px) {
    display: flex;
 
  }
}